@charset "UTF-8";

@font-face {
  font-family: "icon-set";
  src:url("fonts/icon-set.eot");
  src:url("fonts/icon-set.eot?#iefix") format("embedded-opentype"),
    url("fonts/icon-set.woff") format("woff"),
    url("fonts/icon-set.ttf") format("truetype"),
    url("fonts/icon-set.svg#icon-set") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "icon-set" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "icon-set" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow-next:before {
  content: "\3e";
}
.icon-arrow-prev:before {
  content: "\3c";
}
.icon-close:before {
  content: "\58";
}
.icon-menu:before {
  content: "\4d";
}
.icon-logo-thick:before {
  content: "\53";
}
.icon-logo:before {
  content: "\73";
}
